import InfoTable from "../../../plugin/components/InfoTable";
import PageInfo from "../../../plugin/script/common/PageInfo";
import InfoTableItem from "../../../plugin/components/InfoTableItem";
import InputArea from "../../../plugin/components/InputArea";
export default {
  name: "vip_robot_config",
  components: {
    InfoTableItem,
    InfoTable,
    InputArea
  },
  data() {
    return {
      pagesize: 10,
      currentPage: 1,
      total: 0,
      tableData: [],
      dialogFormVisible: false,
      dialogVisible: false,
      formLabelWidth: "120px",
      form: {
        min_coins: "",
        max_coins: "",
        vip_0: "",
        vip_1: "",
        vip_2: "",
        vip_3: "",
        vip_4: "",
        vip_5: "",
        vip_6: ""
      },
      dialogTitle: "",
      rankRobotProbList: {},
      id: 0,
      keys: ""
    };
  },
  async created() {
    //获取数据
    // let { data } = await this.$http.HallFunConfig.GetServerConfig({
    //   key: "robot_rank_vip.lua"
    // });
    let { data } = await this.$http.get(
      "v1/backend/lobby/server_config/robot_rank_vip",
      {
        params: {
          key: "robot_rank_vip.lua"
        }
      }
    );
    console.log(data);
    this.id = data.data[0].id;
    this.keys = data.data[0].sys_key;
    let res = JSON.parse(data.data[0].sys_val);
    console.log(res);
    this.rankRobotProbList = res;
  },
  filters: {
    formatRate: function(obj) {
      var rantStr = "";
      for (var key in obj) {
        rantStr += key + "=" + obj[key] + ",";
      }
      return rantStr;
    }
  },
  methods: {
    sendDataToServer() {
      let data = {
        type_id: 3
      };
      this.$http.post("v1/backend/lobby/server_config_two", data).then(res => {
        console.log(res);
        if (res.data.code === 1) {
          this.$message({
            type: "success",
            message: res.data.msg
          });
        }
      });
    },
    resetForm() {
      this.form = {
        min_coins: "",
        max_coins: "",
        vip_0: "",
        vip_1: "",
        vip_2: "",
        vip_3: "",
        vip_4: "",
        vip_5: "",
        vip_6: ""
      };
    },
    getRobotConfList() {
      this.$http
        .get("v1/backend/lobby/robot", {
          params: {
            page: this.currentPage,
            limit: this.pagesize
          }
        })
        .then(res => {
          console.log(res);
          this.tableData = res.data.data;
          this.total = res.data.total;
        });
    },
    openAddDialog() {
      this.dialogFormVisible = true;
      this.dialogTitle = "添加信息";
      this.resetForm();
    },
    openRankDialog() {
      this.dialogVisible = true;
      this.get();
    },
    addNewRobotConf() {
      if (!this.form.robot_id) {
        let rate = {
          vip_0: this.form.vip_0,
          vip_1: this.form.vip_1,
          vip_2: this.form.vip_2,
          vip_3: this.form.vip_3,
          vip_4: this.form.vip_4,
          vip_5: this.form.vip_5,
          vip_6: this.form.vip_6
        };
        let data = {
          min_coins: Number(this.form.min_coins),
          max_coins: Number(this.form.max_coins),
          vip_rate: JSON.stringify(rate)
        };
        this.$http.post("v1/backend/lobby/robot", data).then(res => {
          console.log(res);
          if (res.data.code === 1) {
            this.dialogFormVisible = false;
            this.getRobotConfList();
          }
        });
      } else {
        let rate = {
          vip_0: this.form.vip_0,
          vip_1: this.form.vip_1,
          vip_2: this.form.vip_2,
          vip_3: this.form.vip_3,
          vip_4: this.form.vip_4,
          vip_5: this.form.vip_5,
          vip_6: this.form.vip_6
        };
        let data = {
          min_coins: Number(this.form.min_coins),
          max_coins: Number(this.form.max_coins),
          vip_rate: JSON.stringify(rate),
          robot_id: this.form.robot_id
        };
        this.$http.put("v1/backend/lobby/robot", data).then(res => {
          console.log(res);
          if (res.data.code === 1) {
            this.dialogFormVisible = false;
            this.getRobotConfList();
          }
        });
      }
    },
    handleEdit(row) {
      console.log(row);
      this.dialogTitle = "更新信息";
      this.dialogFormVisible = true;
      this.form.robot_id = row.id;
      this.form.min_coins = row.min_coins;
      this.form.max_coins = row.max_coins;
      this.form.vip_0 = JSON.parse(row.vip_rate).vip_0;
      this.form.vip_1 = JSON.parse(row.vip_rate).vip_1;
      this.form.vip_2 = JSON.parse(row.vip_rate).vip_2;
      this.form.vip_3 = JSON.parse(row.vip_rate).vip_3;
      this.form.vip_4 = JSON.parse(row.vip_rate).vip_4;
      this.form.vip_5 = JSON.parse(row.vip_rate).vip_5;
      this.form.vip_6 = JSON.parse(row.vip_rate).vip_6;
    },
    handleDelete(row) {
      console.log(row);
      this.$confirm("确认删除吗?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$http
            .delete("v1/backend/lobby/robot", {
              params: {
                robot_id: row.id
              }
            })
            .then(res => {
              console.log(res);
              if (res.data.code === 1) {
                this.getRobotConfList();
              }
            });
          this.$message({
            type: "success",
            message: "删除成功!"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    async send() {
      this.loading = true;
      let params = {
        keys: this.keys,
        values: JSON.stringify(this.rankRobotProbList),
        id: this.id
      };
      // let { data } = await this.$http.HallFunConfig.PostServerConfig({
      //   keys: this.keys,
      //   values: JSON.stringify(this.rankRobotProbList),
      //   id: this.id
      // });
      let { data } = await this.$http.post(
        "/v1/backend/lobby/server_config/robot_rank_vip",
        params
      );
      console.log(data);
      if (data.code === 1 && data.msg === "ok") {
        this.loading = false;
        this.$message({
          type: "success",
          message: "发送服务器配置成功!"
        });
      }
    },
    async get() {
      let { data } = await this.$http.get(
        "v1/backend/lobby/server_config/robot_rank_vip",
        {
          params: {
            key: "robot_rank_vip.lua"
          }
        }
      );
      console.log(data);
    },
    async save() {
      let params = {
        keys: this.keys,
        values: JSON.stringify(this.rankRobotProbList),
        id: this.id
      }
      let { data } = await this.$http.put('v1/backend/lobby/server_config/robot_rank_vip',params)
      // console.log(data);
      if (data.code === 1 && data.msg === "ok") {
        this.dialogVisible = false;
        this.$message({
          type: "success",
          message: "保存成功!"
        });
      }
    },
    handleSizeChange(val) {
      this.pagesize = val;
      this.getRobotConfList();
    },
    handleCurrentChange(val) {
      this.currentPage = val;
      this.getRobotConfList();
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    open() {
      this.$confirm("确认发送吗?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$message({
            type: "success",
            message: "发送成功!"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消发送"
          });
        });
    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePreview(file) {
      console.log(file);
    }
  },
  mounted() {
    this.getRobotConfList();
  }
};



